@use "../util/" as *;
.hero {
  position: relative;
  overflow: hidden;
  height: 100vh;
}
.section__hero__content {
  max-width: rem(1100);
  margin: auto;
  padding-top: rem(100);

  &__text {
    text-align: left;
    max-width: rem(568);
    &--title {
      font-size: rem(75);
      font-weight: 800;
      line-height: 1.3;
    }
    &--paragraph {
      font-size: rem(20);
      font-weight: 500;
    }
  }
  &__buttons {
    display: flex;
    gap: rem(20);

    &--our-works,
    &--contact {
      font-size: rem(18);
      font-weight: 600;
      color: #fff;
      padding: rem(20) rem(41);
      border-radius: var(--radius-md);
      cursor: pointer;
    }
    &--our-works {
      background-color: var(--red-color);
    }
    &--contact {
      background-color: var(--text-color);
    }
  }
  &__imgs {
    &--yellow-circle {
      max-width: 100%;
      position: absolute;
      top: -9%;
      left: 4%;
      z-index: -999;
    }
    &--person-coding {
      max-width: rem(610);
      max-height: rem(650);
      position: absolute;
      top: 22%;
      right: 16%;
    }
  }

  // 1200px MEDIA
  @include breakpoint-up("large") {
    @include margin-x(40px);

    &__text {
      max-width: rem(468);
      &--title {
        font-size: rem(62);
        font-weight: 800;
        line-height: 1.3;
      }
      &--paragraph {
        font-size: rem(18);
        font-weight: 500;
      }
    }

    &__imgs {
      &--yellow-circle {
        left: -20%;
        top: -20%;
      }
      &--person-coding {
        max-width: rem(410);
        max-height: rem(450);
        right: 5%;
      }
    }
  }

  // 992px MEDIA

  @include breakpoint-up("meduim") {
    &__text {
      max-width: rem(368);
      &--title {
        font-size: rem(49);
        font-weight: 800;
        line-height: 1.3;
      }
      &--paragraph {
        font-size: rem(15);
        font-weight: 500;
      }
    }

    &__buttons {
      gap: rem(10);
      &--our-works,
      &--contact {
        font-size: rem(15);
        font-weight: 600;
        color: #fff;
        padding: rem(14) rem(28);
        border-radius: var(--radius-md);
      }
    }

    &__imgs {
      &--yellow-circle {
        left: -20%;
        top: -30%;
      }
      &--person-coding {
        max-width: rem(300);
        max-height: rem(250);
        right: 5%;
        top: 30%;
      }
    }
  }
  // 640px MEDIA
  @include breakpoint-up("small") {
    &__text {
      max-width: rem(250);
      &--title {
        font-size: rem(33);
        font-weight: 800;
        line-height: 1.3;
      }
      &--paragraph {
        font-size: rem(12);
        font-weight: 500;
      }
    }

    &__buttons {
      gap: rem(5);
      &--our-works,
      &--contact {
        font-size: rem(12);
        font-weight: 600;
        color: #fff;
        padding: rem(13) rem(25);
        border-radius: var(--radius-md);
      }
    }

    &__imgs {
      &--yellow-circle {
        max-width: rem(600);
        left: -40%;
        top: -30%;
      }
      &--person-coding {
        display: none;
      }
    }
  }
}
