@mixin flex($dir: row, $items: center, $content: space-between) {
  display: flex;
  flex-direction: $dir;
  align-items: $items;
  justify-content: $content;
}

@mixin margin-x($value) {
  margin-left: $value;
  margin-right: $value;
}

@mixin margin-y($value) {
  margin-top: $value;
  margin-bottom: $value;
}

@mixin pad-x($value) {
  padding-left: $value;
  padding-right: $value;
}

@mixin pad-y($value) {
  padding-top: $value;
  padding-bottom: $value;
}
