@use "../util/" as *;

.nav {
  max-width: rem(1100);
  @include flex();
  margin: 0 auto;
  padding-top: rem(64);
  &__logo {
    flex: 1;
  }
  &__logo-img {
    width: rem(128);
    height: rem(43);
  }

  &__links {
    @include flex();
    gap: rem(44);

    &--link {
      font-size: rem(15);
      font-weight: 600;
    }
  }
  &__cta--button {
    font-size: rem(15);
    font-weight: 700;
    background-color: var(--main-color);
    padding: rem(14) rem(45);
    border-radius: var(--radius-md);
    &:hover {
      opacity: 0.9;
    }
  }

  &__icon {
    border: none;
    outline: none;
    background: none;
    display: none;

    &__close,
    &__open {
      font-size: rem(40);
    }
  }

  // 1200px media
  @include breakpoint-up("large") {
    @include margin-x(40px);
  }
  // 992px media
  @include breakpoint-up("meduim") {
    &__logo {
      z-index: 9999;
    }
    .nav__links {
      position: absolute;
      background-color: var(--green-color);
      top: 0;
      left: 0;
      margin: 0;
      padding: rem(28);
      width: 50%;
      height: 100vh;
      @include flex(column, space-between, center);
      transition: 0.5s all ease-in-out;
      &.hidden {
        left: -100%;
      }
    }

    &__icon {
      display: block;
      z-index: 9999;
      margin-left: rem(28);
    }

    &__icon .not-active {
      display: none;
    }
  }

  @include breakpoint-up("small") {
    padding-top: rem(40);
    .nav__links li:not(:last-child) {
      display: block;
      color: #fff;
    }

    .nav__links {
      position: absolute;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
      margin: 0;
      background-color: var(--green-color);
      font-size: rem(24);
      @include flex(column, center, center);
      transition: 0.4s ease-in-out;
      &.hidden {
        left: -100%;
      }
    }
  }
}
