@use "../util" as *;

.section__we-are {
  position: relative;
  overflow: hidden;
  padding-top: rem(130);
  margin: 0 auto;
  text-align: center;

  &__heading {
    margin-bottom: rem(86);
  }

  &__services {
    margin: 0 auto;
    @include pad-x(20px);
    max-width: rem(1080);
    @include flex();
    gap: rem(56);

    &--serv {
      max-width: rem(323);
      @include flex(column);
      gap: rem(50);

      &-img-box {
        position: relative;

        &-background {
          position: absolute;
          max-width: rem(130);
          top: 50%;
          left: -50%;
          transform: translatey(-50%);
          z-index: -1;
        }
      }

      &-text-box {
        @include flex(column);
        gap: rem(20);

        &--title {
          font-size: rem(22);
          font-weight: 700;
        }
        &--description {
          font-size: rem(17);
        }
      }
    }
  }
  @include breakpoint-up("meduim") {
    padding-top: rem(40);
    &__services {
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
    }
  }

  @include breakpoint-up("small") {
    &__services {
      flex-direction: column;
      gap: rem(56);
    }
  }

  &__clients {
    max-width: rem(1335);
    margin: 0 auto;
    display: grid;
    //grid-template-columns: repeat(3, 1fr);
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    align-items: center;
    justify-content: end;
    grid-template-areas: "first-half img second-half" "third-half img fourth-half";

    &__box {
      &-numbers {
        margin: 30px 0;
        &-number {
          font-size: rem(75);
          font-weight: 800;
          margin-bottom: rem(-22);
        }
        &-name {
          font-size: rem(22);
          font-weight: 700;
          margin: 0;

          > span {
            position: relative;
            &::before {
              content: "";
              position: absolute;
              width: 100%;
              height: 10px;
              background-color: var(--main-color);
              top: 50%;
              z-index: -1;
            }
          }
        }
      }
      &:first-child {
        grid-area: first-half;
        align-self: end;
      }
      &:nth-child(2) {
        grid-area: third-half;
        align-self: start;
      }
      &:nth-child(3) {
        grid-area: second-half;
        align-self: end;
      }
      &:nth-child(4) {
        grid-area: fourth-half;
        align-self: start;
      }
      &:last-child {
        max-width: rem(700);
        grid-area: img;
        align-self: center;
      }
    }
    &-bg {
      position: absolute;
      width: rem(3690);
      height: rem(3690);
      bottom: 18%;
      left: -50%;
      z-index: -999;
    }

    @include breakpoint-up("large") {
      padding: rem(40);
      &__box {
        &-img {
          max-width: 100%;
        }
      }
    }

    @include breakpoint-up("meduim") {
      margin-top: rem(80);
      grid-template-columns: repeat(3, 250px);
      grid-template-rows: repeat(3, 200px);
      justify-content: center;
      align-items: center;

      &__box {
        &:first-child {
          grid-area: 2/1/3/2;
          align-self: end;
          justify-self: end;
        }
        &:nth-child(2) {
          grid-area: 3/1/4/2;
          align-self: start;
          justify-self: end;
        }
        &:nth-child(3) {
          grid-area: 2/3/3/4;
          align-self: end;
          justify-self: start;
        }
        &:nth-child(4) {
          grid-area: 3/3/4/4;
          align-self: start;
          justify-self: start;
        }
        &:last-child {
          max-width: 40%;
          grid-area: 1/1/2/4;
          align-self: end;
          justify-self: center;
        }

        &-numbers {
          &-number {
            font-size: rem(65);
          }
          &-name {
            font-size: rem(18);
          }
        }
      }

      &-bg {
        display: none;
      }
    }
    @include breakpoint-up("small") {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-content: center;
      &__box {
        margin: 0 auto;

        &-numbers {
          margin: -10px 0;
        }
        &:last-child {
          max-width: 80%;
          order: -1;
        }
      }
    }
  }
}
