/// 640px , 992px, 1200px
$breakpoints-up: (
  "small": 40em,
  "meduim": 62em,
  "large": 75em,
);

/// 639px , 991px, 1199px
$breakpoints-down: (
  "small": 39.9375em,
  "meduim": 61.9375em,
  "large": 74.9375em,
);

/// Responsive MIXIN
///
/// ******MAX-MIXIN*******
///
///  @param > $breakpoint > {String}  - Breakpoint name {'small', 'medium', 'large', ...}
/// @require $breakpoints and it was in _breakpoints.scss file

@mixin breakpoint-up($breakpoint) {
  /// If the key $breakpoint exists in the map
  @if map-has-key($breakpoints-up, $breakpoint) {
    // Prints The media query That based on its value
    @media (max-width: map-get($breakpoints-up, $breakpoint)) {
      @content;
    }
    // If the key doesn't exist in the map
  } @else {
    @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Available breakpoints are: #{map-keys($breakpoints)}.";
  }
}
/// ******MIN-MIXIN*******
@mixin breakpoint-down($breakpoint) {
  @media (min-width: map-get($breakpoints-down, $breakpoint)) {
    @content;
  }
}
