@use "fonts" as *;
@use "colors" as *;
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--fonstss);
  color: var(--text-color);
}

li {
  list-style: none;
}

button {
  background: none;
  border: none;
  outline: none;
}
