@use "../util" as *;

// Section Title
.section-heading {
  max-width: rem(850);
  margin: 0 auto;
  text-align: center;

  &-sub-title-box {
    &-title {
      position: relative;
      font-size: rem(18);
      font-weight: 700;
    }

    &-img {
      position: absolute;
      top: -65%;
      left: 48.5%;
      max-width: 100%;
      z-index: -1;
    }
  }

  &-title {
    font-size: rem(42);
    font-weight: 800;
    line-height: 1.4;
    margin-top: -20px;
  }

  span {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: rem(14);
      top: 60%;
      left: 0%;
      background-color: var(--main-color);
      z-index: -1;
    }
  }

  @include breakpoint-up("meduim") {
    max-width: 80%;
    &-title {
      font-size: rem(26);
    }
  }

  @include breakpoint-up("small") {
    &-title {
      margin-top: -20px;
    }
    &-sub-title-box {
    }
  }
}
